import { CircularProgress } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import React, { useState } from "react";
import useSound from "use-sound";

const DrawerContent = ({
    price,
    theme,
    children,
    logo,
    title,
    upgrade,
    axios,
    setSnackBar,
    setBalance,
    balance,
    setUpgradeDrawer,
    chargePerSecond,
    coinsPerTap,
    maxEnergy,
}) => {
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const upgrades = {
        charge: "chargePerSecond",
        tap: "coinsPerTap",
        energy: "maxEnergy",
    };
    const click_sound = "/assets/sounds/click2.mp3";
    const [playClickSound] = useSound(click_sound);
    const details = {
        price: Math.floor(
            upgrade[0] == "charge"
                ? 2 ** (upgrade[1] + price)
                : 2 ** ((upgrade[1] + price) / price)
        ) * 1024,
        whatToUpgrade: upgrades[upgrade[0]],
    };
    return (
        <Grid
            container="true"
            display={"flex"}
            direction={"row"}
            justifyContent={"center"}
        >
            <Grid>
                <h1
                    style={{
                        top: 0,
                    }}
                >
                    {title}
                </h1>
                <p
                    style={{
                        textAlign: "start",
                        padding: "6px",
                    }}
                >
                    {children}
                </p>
            </Grid>
            <Grid
                sx={{
                    left: 0,
                    right: 0,
                    marginBottom: ".55rem",
                }}
            >
                <button
                    className={`btn ${theme}`}
                    style={{
                        padding: "10px",
                        color: "#fff",
                    }}
                    disabled={loading}
                    onClick={onClick}
                >
                    <Grid
                        style={{
                            fontSize: "30px",
                            fontFamily: "mortal-kombat",
                        }}
                        container="true"
                        display={"flex"}
                        alignItems={"center"}
                    >
                        <Grid>
                            {!loading ? (
                                <img
                                    src={logo}
                                    alt=""
                                    height={"52px"}
                                    width={"52px"}
                                />
                            ) : (
                                <CircularProgress />
                            )}
                        </Grid>
                        <Grid marginLeft={".2rem"}>
                            {!loading ? (
                                <span>
                                    {format(
                                        parseInt(details.price)
                                    ).toLocaleString()}{" "}
                                    Koins
                                </span>
                            ) : (
                                <span>Fetching data</span>
                            )}
                        </Grid>
                    </Grid>
                </button>
            </Grid>
        </Grid>
    );

    function onClick() {
        if (error) return setSnackBar(["", "", false]);

        setLoading(true);
        axios
            .post("/upgrade/upgrade", {
                item: details.whatToUpgrade,
            })
            .then(({ data }) => {
                setBalance(balance - parseInt(details.price));
                setSnackBar(["Upgraded successfully.", "success", true, false]);
                setUpgradeDrawer([false, <></>]);
                eval(`${details.whatToUpgrade}(${data.result})`);
                playClickSound();
            })
            .catch((error) => {
                if (
                    error.response &&
                    error.response.data &&
                    error.response.data.message == "Insufficient balance."
                ) {
                    // setUpgradeDrawer([false, <></>]);
                    return setSnackBar([
                        "Insufficient balance.",
                        "error",
                        true,
                    ]);
                }
                // setUpgradeDrawer([false, <></>]);
                console.log(error);
                setSnackBar(["Error fetching data.", "error", true]);
            })
            .finally(() => setLoading(false));
    }

    function format(number) {
        const formatter = new Intl.NumberFormat("en", {
            notation: "compact",
            compactDisplay: "short",
            maximumSignificantDigits: 3,
        });

        return formatter.format(number);
    }
};

export default DrawerContent;
